/** TABLES **/

.table {
  @apply shadow-lg bg-gray-700 border-separate border-spacing-0  rounded-lg overflow-hidden;
}

.th,
.td {
  @apply border-gray-600 px-8 py-4;
}

.th {
  @apply border-b-4  bg-gray-900 text-center relative;
}

.td {
  @apply border-r border-l;
}

.th:first-child,
.td:first-child {
  @apply border-l-0;
}

.th:last-child,
.td:last-child {
  @apply border-r-0;
}

.tr:nth-child(odd) td {
  @apply bg-gray-800;
}

.tr:nth-child(even) td {
  @apply bg-gray-900;
}
.resizer {
  @apply absolute z-40 top-0 h-full  bg-gray-600;
  /* cursor-col-resize; */
  width: 4px;
  right: -2px;
  user-select: none;
  touch-action: none;
}

.th:last-child .resizer {
  @apply bg-transparent;
  right: 0px;
}

.resizer.isResizing {
  @apply bg-gray-500;
}

.columnDragger {
  @apply mr-2;
}

.headerFlex {
  @apply flex items-center min-w-max;
}
.headerFlexSpacer {
  @apply grow;
}
.dropdown {
  @apply relative inline-block;
}

.dropdownList {
  @apply absolute left-0 top-full bg-gray-800 rounded-lg shadow-lg w-44 z-50 text-left;
}

.dropdownList li {
  @apply py-2 cursor-pointer hover:bg-gray-700 rounded-lg p-3 text-gray-400;
}

.sortDiv {
  @apply /* cursor-pointer */
   select-none;
}
