.menuSideBar {
  @apply w-80 h-full m-0 bg-gray-800 shadow-lg;
}

.title {
  @apply flex flex-col justify-center 
    m-0 p-2;
}

.titleText {
  @apply text-lg tracking-wider font-bold 
    text-gray-400 
    mr-auto ml-4 my-auto align-middle;
}

.subTitle {
  @apply text-sm text-gray-400;
}
.subTitle::before {
  content: '## ';
}

.arrow {
  @apply my-auto mr-2 
     transition-all duration-100 ease-linear;
}

.arrowRotated {
  transform: rotate(90deg);
}

.menuSideBarDropdowns {
  @apply flex flex-col items-center justify-start 
    p-1 m-0;
}

.container {
  @apply flex w-full flex-row;
}

.dropdown {
  @apply m-0  px-2 pb-2
  flex-grow 
  text-gray-500;
}

.dropdownHeader {
  @apply flex flex-row
    mx-0 mt-2
    text-gray-500
    hover:text-gray-400
    transition duration-200 ease-in-out
    cursor-pointer;
}

.dropdownHeaderSelected {
  @apply text-gray-400;
}

.dropdownHeaderText {
  @apply text-lg font-semibold;
}

.dropdownPlus {
  @apply p-2 mt-2
  text-gray-500
  cursor-pointer;
}

.dropdownPlus:hover {
  @apply bg-gray-600 rounded-full text-gray-400;
}

.subMenuItem {
  @apply flex flex-row items-center justify-evenly
    mt-2 ml-2
    transition-all duration-200 ease-in-out 
    hover:text-gray-400
    cursor-pointer;
}

.subMenuItemSelected {
  @apply flex flex-row items-center justify-evenly
    mt-2 ml-2
    text-gray-400;
}

.subMenuBullet {
  @apply w-1 h-1 mr-2 rounded bg-gray-500
  transition-all duration-200 ease-in-out;
}

.subMenuBulletSelected {
  @apply w-2 h-2 mr-2 rounded bg-gray-400;
}

.subMenuText {
  @apply font-semibold tracking-wide
    w-full;
}

.subMenuItemSelected {
  @apply text-gray-400;
}

.plusContainer {
  @apply h-min relative;
}

.tooltip {
  @apply absolute w-auto p-2 min-w-max rounded-md shadow-md
    text-white bg-gray-900
    text-xs font-bold 
    transition-all duration-100 scale-0
    bottom-1/2 left-1/2  -translate-x-1/2 -translate-y-1/2
    origin-bottom;
}
